@use 'sass:math';
.pagging {
	display: flex;
	align-items: center;
	justify-content: center;
	// .pagging__arrow
	&__arrow {
		width: 24px;
		height: 24px;
		display: block;

		transition: transform 0.3s ease 0s;

		@media (any-hover: hover){
			&:hover{
				transform: scale(1.2);
			}
		}
		&_next {
			background: url('../img/icons/sprites/sprite.svg#pagging-arrow-right') 0 0 / cover no-repeat;
		}
		&_prev {
			margin-right: 28px;

			background: url('../img/icons/sprites/sprite.svg#pagging-arrow-left') 0 0 / cover no-repeat;
		}
	}
	// .pagging__list
	&__list {
		display: flex;
		margin-right: 28px;
		li {
			&:not(:last-child){
				margin:0px 8px 0px 0px;
			}
		}
	}
	// .pagging__item
	&__item {
		padding: 3px 12px;

		line-height: math.div(26px,16px) * 100%;
		font-weight: 600;
		color: var(--add-gray);

		transition: color 0.3s ease 0s;
		&._active {
			border: 1px solid var(--main-yellow);
			border-radius: 4px;
		}

		@media (any-hover: hover){
			&:hover{
				color: var(--main-black);
			}
		}
	}
}
