@use 'sass:math';

.catalogue {

    // .catalogue__container
    &__container {}

    // .catalogue__title
    &__title {}

    // .catalogue__content
    &__content {
        display: grid;
        grid-template-columns: 256px 1fr;
        grid-column-gap: 50px;

        @media (max-width:$md2+px){
            display: block;
        }
    }

    // .catalogue__sidebar
    &__sidebar {}

    // .catalogue__items
    &__items {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
        grid-gap: 64px 15px;

        margin-bottom: 50px;

        @media (max-width:650px){
            grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
        }
        @media (max-width:$md4+px){
            justify-items: stretch;
            grid-template-columns: repeat(auto-fill, minmax(136px, 1fr)); 
        }
    }
    // .catalogue__info
    &__info {
        display: flex;
        justify-content:space-between;
        align-items:flex-start;
        margin-bottom: 40px;

        @media (max-width:$md2+px){
            margin-bottom: 15px;
        }
    }
    // .catalogue__selected
    &__selected {
        flex: 1 1 auto;

        display: flex;
        align-items:center;
        flex-wrap: wrap;
        margin: 0 -8px;

        @media (max-width:$md2+px){
            margin: 0 -5px 25px;
       }
    }
    // .catalogue__item-selected
    &__item-selected {
        padding: 8px;

        &:not(:last-child){
           padding: 5px;
        }
    }
    // .catalogue__item-body
    &__item-body {
        display: flex;
        justify-content:space-between;
        align-items:center;
        padding: 6px 12px;

        font-weight: 600;
        line-height: math.div(26px,16px) * 100%;

        border: 2px solid var(--main-yellow);

        @media (max-width:$md4+px){
             padding: 6px 10px;

             font-size: 13px;
        }

        span {
            margin-right: 12px;
        }
    }
    // .catalogue__icon
    &__icon {
        display:flex;
        justify-content:center;
        align-items: center;

        cursor: pointer;
    }
    // .catalogue__sort
    &__sort {
        flex: 0 0 200px;
        @media (max-width:$md4+px){
             flex: 0 0 165px; 
        }
    }
    // .catalogue__label-sort
    &__label-sort {
    }
    // .catalogue__form-sort
    &__form-sort {
    }
    // .catalogue__pagging
    &__pagging {
        display: flex;
        justify-content:space-between;
        align-items:center;
        margin-bottom: 106px;
        @media (max-width:$md4+px){
              margin-bottom: 64px;
        }
    }
    // .catalogue__show-more-btn
    &__show-more-btn {
        padding: 12px 100px;

        line-height: math.div(26px,16px) * 100%;
        letter-spacing: math.div(.3px,16px) * 1em;
        font-weight: 800;
        color: var(--main-yellow);

        background-color: #fff;
        border: 2px solid var(--main-yellow);

        transition: border-color 0.3s ease 0s,
		background-color 0.3s ease 0s;

        @media (any-hover: hover){
            &:hover{
                background-color: var(--main-yellow);
                color: #fff;
            }
        }
        
    }
    // .catalogue__mob-filter
    &__mob-filter {
        display: none;
        @media (max-width:$md2+px){
            flex: 0 0 95px;
            display: flex;
            align-items:center;

            cursor: pointer;
        }
    }
    // .catalogue__mob-label
    &__mob-label {
        @media (max-width:$md2+px){
            margin-right: 8px;

            font-size: 14px;
            line-height: math.div(26px,14px) * 100%;
            color: var(--add-gray);
    
        }
    }
}

.sidebar-catalogue {


    // .sidebar-catalogue__rows
    &__rows {
        display: flex;
        flex-direction: column;
        @media (max-width:$md2+px){
            padding: 0 48px;
        }
        @media (max-width:$md4+px){
             padding: 0 16px; 
        }
    }

    // .sidebar-catalogue__row
    &__row {
        &:not(:last-child) {
            margin: 0px 0px 32px 0px;
        }
    }

    // .sidebar-catalogue__title
    &__title {
        display: flex;
        justify-content: space-between;
        align-items: center;

        margin-bottom: 24px;

        font-size: 19px;
        line-height: math.div(24px, 19px) * 100%;
        letter-spacing: math.div(.3px, 19px) * 1em;
        font-weight: 700;
        cursor: pointer;

        @media (max-width:$md3+px) {
            margin-bottom: 16px;
        }

        &::after {
            content: "";
            display: block;
            margin-left: 5px;
            width: 24px;
            height: 24px;
            background: url('../img/icons/sprites/sprite.svg#spoller-arrow') 0 0 / cover no-repeat;
        }
    }

    // .sidebar-catalogue__content
    &__content {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }

    // .sidebar-catalogue__price-names
    &__price-names {
        display: flex;
        align-items: center;
        width: 100%;
        margin-bottom: 6px;
    }

    // .sidebar-catalogue__price-name
    &__price-name {
        font-size: 13px;
        line-height: math.div(21px, 13px) * 100%;

        &:first-child {
            flex: 0 1 55%;
        }
    }

    // .sidebar-catalogue__price-inputs
    &__price-inputs {
        display: flex;
        justify-content: space-between;
        align-items: center;

        // border: 1px solid var(--light-white);
    }

    // .sidebar-catalogue__input
    &__input {
        width: 45%;
        flex: 0 1 45%;
        padding: 13px;

        line-height: math.div(26px, 16px) * 100%;
        font-weight: 600;
        border: none;
        color: var(--add-gray);



    }

    // .sidebar-catalogue__separator
    &__separator {}

    // .sidebar-catalogue__label
    &__label {
        padding-left: 32px;

        line-height: math.div(26px, 16px) * 100%;

        &:not(:last-child) {
            margin: 0px 8px 0px 0px;
        }

        @media (max-width:$md4+px) {
            font-size: 14px;
        }
    }

    // .sidebar-catalogue__checkbox
    &__checkbox {
        &._checkbox {
            &::before {
                top: 0;
                left: -32px;
            }

            &::after {
                left: -26.5px;
                top: 2.8px;
            }
        }
    }

    // .sidebar-catalogue__show-more
    &__show-more {
        margin-top: 16px;

        line-height: math.div(26px, 16px) * 100%;
        letter-spacing: math.div(.3px, 16px) * 1em;
        font-weight: 800;
        color: var(--main-yellow);

        background-color: transparent;

        transition: color 0.3s ease 0s;

        @media (any-hover: hover) {
            &:hover {
                color: var(--hover-color);
            }
        }
    }

    // .sidebar-catalogue__countries
    &__countries {
        margin-bottom: 32px;
    }

    // .sidebar-catalogue__countries-content
    &__countries-content {
        display: flex;
        flex-wrap: wrap;
    }

    // .sidebar-catalogue__countries-label
    &__countries-label {
        flex: 0 1 68px;
        display: flex;
        flex-direction: column;
        align-items: center;
        cursor: pointer;

        @media (max-width:$md4+px){
             flex: 0 1 35%; 
        }

        &:not(:last-child){
            margin:0px 5px 24px 0px;
        }
    }

     // .sidebar-catalogue__countries-image
     &__countries-image {
        margin-bottom: 7px;
        border: 2px solid transparent;

        transition: border-color 0.3s ease 0s;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
     }

    // .sidebar-catalogue__countries-input
    &__countries-input {
        &:checked {
            & + .sidebar-catalogue__countries-image {
                border-color: var(--main-yellow);
            }
        }
    }

    // .sidebar-catalogue__countries-name
    &__countries-name {
        font-size: 13px;
        line-height: math.div(21px,13px) * 100%;
    }

    // .sidebar-catalogue__country-search
    &__country-search {
        position: relative;
        margin-bottom: 16px;
    }

    // .sidebar-catalogue__switch
    &__switch {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 32px;
    }

    // .sidebar-catalogue__switch-title
    &__switch-title {
        font-size: 19px;
        line-height: math.div(24px, 19px) * 100%;
        letter-spacing: math.div(.3px, 19px) * 1em;
        font-weight: 700;
        cursor: pointer;
    }
    // .sidebar-catalogue__label-switch
    &__label-switch {   
    }

    // .sidebar-catalogue__mob-title
    &__mob-title {
        display: none;
        @media (max-width:$md2+px){
            display: inline-flex;
            justify-content: center;
            width: 100%;
            margin-bottom: 24px;

            font-size: 19px;
            line-height: math.div(24px,19px) * 100%;
            letter-spacing: math.div(.3px,19px) * 1em;
            font-weight: 700;
        }
    }
    // .sidebar-catalogue__mob-btn
    &__mob-btn {
       display: none;
       @media (max-width:$md2+px){
           position: fixed;
           bottom: 0;
        display: flex;
        justify-content:center;
        align-items: center;
        width: 100%;
        max-width: 480px;
        margin: 0px auto;
        padding: 13px 0;

        line-height: math.div(26px,16px) * 100%;
        letter-spacing: math.div(.3px,16px) * 1em;
        font-weight: 800;
        color: #fff;

        background-color: var(--main-yellow);

        span {
            margin: 0 5px;
        }
       }
    }
}


