@use 'sass:math';

.comparison {

    // .comparison__products
    &__products {
        margin-bottom: 64px;
    }

    // .comparison__info
    &__info {}
}

.products-comparison {

    // .products-comparison__container
    &__container {}

    // .products-comparison__title
    &__title {}

    // .products-comparison__content
    &__content {
        display: grid;
        grid-template-columns: 256px calc(100% - 256px);

        @media (max-width:$md3+px) {
            display: block;
        }
    }

    // .products-comparison__sidebar
    &__sidebar {
        padding-top: 46px;

        @media (max-width:$md3+px) {
            display: none;
        }
    }

    // .products-comparison__body
    &__body {
        position: relative;
    }

    // .products-comparison__info-row
    &__info-row {
        margin-bottom: 24px;
    }

    // .products-comparison__items
    &__items {
        @media (max-width:$md4+px) {
            &._swiper {
                overflow: visible;
            }
        }
    }

    // .products-comparison__slide
    &__slide {
        @media (min-width: 1387px) {
            flex: 0 1 33.333%;
        }

        @media (max-width:$md4+px) {
            .products-comparison__label {
                opacity: 0;
                visibility: hidden;
            }

            .products-comparison__name {
                opacity: 0;
                visibility: hidden;
            }

            &.swiper-slide-active {
                .products-comparison__label {
                    opacity: 1;
                    visibility: visible;
                }

                .products-comparison__name {
                    opacity: 1;
                    visibility: visible;
                }
            }

        }
    }

    // .products-comparison__card
    &__card {
        margin-bottom: 71px;
    }

    // .products-comparison__info
    &__info {}

    // .products-comparison__category
    &__category {
        &:not(:last-child) {
            margin: 0px 0px 66px 0px;

            @media (max-width:$md4+px) {
                margin-bottom: 24px;
            }
        }
    }

    // .products-comparison__data
    &__data {
        padding: 12px 0;

        font-size: 13px;
        line-height: math.div(21px, 13px) * 100%;

        &:nth-child(4n) {
            background-color: var(--light-gray);
        }

        @media (max-width:$md4+px) {
            margin: 0 -30px;
            padding-left: 30px;
            background-color: var(--light-gray);
        }

    }

    // .products-comparison__controls
    &__controls {}

    // .products-comparison__scroll-bar
    &__scroll-bar {
        position: absolute;
        z-index: 2;
        top: 589px;
        cursor: pointer;

        width: 100%;
        height: 6px;
        background-color: var(--light-gray);

        @media (max-width:$md4+px) {
            top: 385px;
        }

        .swiper-scrollbar-drag {
            height: 100%;
            background-color: #dfdfdf;
        }
    }

    // .products-comparison__label
    &__label {
        display: none;

        @media (max-width:$md4+px) {
            display: block;
            margin-bottom: 16px;

            font-size: 16px;
            font-weight: 600;
            line-height: math.div(24px, 16px) * 100%;
            letter-spacing: math.div(.3px, 16px) * 1em;
            white-space: nowrap;

            transition: opacity 0.3s ease 0s,
                visibility 0.3s ease 0s;
        }
    }

    // .products-comparison__name
    &__name {
        display: none;

        @media (max-width:$md4+px) {
            display: block;
            padding: 8px 0;
            font-size: 13px;
            line-height: math.div(21px, 13px) * 100%;
            color: var(--add-gray);

            transition: opacity 0.3s ease 0s,
                visibility 0.3s ease 0s;
        }
    }
}

.sidebar-comparison {

    // .sidebar-comparison__top
    &__top {
        max-width: calc(100% - 50px);
        margin-bottom: 436px;
    }

    // .sidebar-comparison__main
    &__main {}

    // .sidebar-comparison__switch
    &__switch {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 24px;
    }

    // .sidebar-comparison__title
    &__title {
        margin-bottom: 8px;

        font-size: 19px;
        line-height: math.div(24px, 19px) * 100%;
        letter-spacing: math.div(.3px, 19px) * 1em;
        font-weight: 700;

        @media (max-width:$md4+px) {
            font-size: 16px;
        }
    }

    // .sidebar-comparison__label
    &__label {
        line-height: math.div(26px, 16px) * 100%;
        font-weight: 600;

        // .sidebar-comparison__label_with-spoller
        &_with-spoller {
            display: flex;
            justify-content: space-between;
            align-items: center;
            cursor: pointer;

            &::after {
                content: "";
                display: block;
                margin-left: 5px;
                width: 24px;
                height: 24px;
                background: url(../img/icons/sprites/sprite.svg#spoller-arrow) 0 0/cover no-repeat;
            }
        }
    }

    // .sidebar-comparison__switch-title
    &__switch-title {}

    // .sidebar-comparison__label-switch
    &__label-switch {}

    // .sidebar-comparison__row
    &__row {}

    // .sidebar-comparison__info-block
    &__info-block {
        display: none;
    }

    // .sidebar-comparison__category
    &__category {
        &:not(:last-child) {
            margin: 0px 0px 40px 0px;
        }
    }

    // .sidebar-comparison__name
    &__name {
        padding: 12px 50px 12px 8px;
        font-size: 13px;
        line-height: math.div(21px, 13px) * 100%;
        color: var(--add-gray);

        &:nth-child(odd) {
            background-color: var(--light-gray);
        }
    }
}

.info-comparison {

    // .info-comparison__container
    &__container {}

    // .info-comparison__content
    &__content {}

    // .info-comparison__row
    &__row {}

    // .info-comparison__title
    &__title {}
}

// .info-row
.info-row {
    display: flex;
    align-items: center;
    justify-content: space-between;

    // .info-row__quantity
    &__quantity {
        font-size: 13px;
        line-height: math.div(21px, 13px) * 100%;
        color: var(--dark-gray);
    }

    // .info-row__clean
    &__clean {
        line-height: math.div(26px, 16px) * 100%;
        font-weight: 800;
        color: var(--main-yellow);
        background-color: transparent;

        transition: color 0.3s ease 0s;

        @media (any-hover: hover) {
            &:hover {
                color: var(--hover-color);
            }
        }
    }
}