@use 'sass:math';

.cart {

    // .cart__goods-cart
    &__goods-cart {
        margin-bottom: 48px;
    }
    //.cart__similar-goods 
    &__similar-goods {
        margin-bottom: 96px;
        @media (max-width:$md4+px){
             margin-bottom: 48px; 
        }
    }
}

.goods-cart {

    // .goods-cart__container
    &__container {}

    // .goods-cart__title
    &__title {
        margin-bottom: 48px;
        @media (max-width:$md4+px){
             margin-bottom: 8px; 
        }
    }

    // .goods-cart__info-row
    &__info-row {
        margin-bottom: 27px;
    }

    // .goods-cart__content
    &__content {
        @media (min-width:$md2+px){
            display: grid;
            grid-template-columns: 1fr math.div(320,1280) * 100%;
            grid-column-gap: 64px; 
        }

        @media (max-width:$md3+px) {
            display: block;
        }
    }

    // .goods-cart__items
    &__items {
        @media (max-width:$md2+px){
            margin-bottom: 32px;
        }
    }

    // .goods-cart__item
    &__item {
        display: flex;
        align-items:flex-start;
    }

    // .goods-cart__promocode
    &__promocode {}
}

.info-row {

    // .info-row__quantity
    &__quantity {}

    // .info-row__clean
    &__clean {}
}

.item-cart {

    // .item-cart__column
    &__column {
        flex: 0 1 192px;

        display: flex;
        flex-direction: column;
        margin-right: 32px;

        @media (max-width:$md1+px){
             flex: 0 1 230px;
             margin-right: 25px; 
        }
        @media (max-width:$md3+px){
             flex: 1 1 39.5%; 
             margin-right: 40px;
        }
        @media (max-width:$md4+px){
             margin-right: 10px; 
        }

    }

    // .item-cart__info
    &__info {
        flex: 0 1 math.div(404, 960) * 100%;
        margin-right: 20px;

        @media (max-width:$md3+px){
            display: none; 
       }
    }

    // .item-cart__quantity
    &__quantity {
        display: flex;
        align-items:center;
        justify-content: space-between;
        margin-right: 20px;
        @media (max-width:$md3+px){
             margin-right: 0; 
        }
    }

    // .item-cart__right-column
    &__right-column {
        flex: 0 1 332px;
        display: flex;
        justify-content:space-between;

        @media (max-width:$md3+px){
             flex-direction: column; 
            //  flex: 0 1 60.5%;
             flex: 0 1 auto;
        }
    }

    // .item-cart__image
    &__image {
        margin-bottom: 15px;

        @media (max-width:$md3+px){
             margin-bottom: 0;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    // .item-cart__set
    &__set {
        display: flex;
        align-items: center;

        color: var(--ligther-green);
        font-size: 13px;
        line-height: math.div(21px, 13px) * 100%;

        @media (max-width:$md3+px){
             margin-bottom: 16px; 

             font-size: 10px;
        }
    }

    // .item-cart__icon
    &__icon {
        width: 24px;
        height: 24px;
        margin-right: 8px;

        @media (max-width:$md4+px){
             width: 16px;
             height: 21px;
             margin-right: 4px; 
             padding-bottom: 5px;
        }

        svg {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    // .item-cart__row
    &__row {
        font-size: 10px;
        line-height: math.div(21px, 10px) * 100%;

        &:not(:last-child) {
            margin: 0px 0px 5px 0px;
        }
    }

    // .item-cart__name
    &__name {
        margin-bottom: 12px;

        line-height: math.div(26px, 16px) * 100%;
        font-weight: 600;

        @media (max-width:$md4+px) {
            margin-bottom: 4px;
            max-width: 160px;

            font-size: 10px;
        }
    }

    // .item-cart__color
    &__color {}

    // .item-cart__size
    &__size {}

    // .item-cart__frame
    &__frame {}

    // .item-cart__brakes
    &__brakes {}

    // .item-cart__wheels
    &__wheels {}

    // .item-cart__price
    &__price {
        flex: 0 0 77px;
        display: flex;
        flex-direction: column;
        align-items: flex-end;

        @media (max-width:$md3+px){
             flex: 0 1 auto;
             align-items: flex-start; 
             margin-bottom: 8px;
        }
    }

    // .item-cart__new
    &__new {
        line-height: math.div(26px,16px) * 100%;
        letter-spacing: math.div(.3px,16px) * 1em;
        font-weight: 800;

        span {
            margin-left: 8px;
            font-weight: 600;
            color: var(--dark-gray);
        }
    }

    // .item-cart__old
    &__old {
        margin-top: 6px;

        font-size: 13px;
        line-height: math.div(21px,13px) * 100%;
        font-weight: 600;
        color: var(--medium-gray);

        @media (max-width:$md4+px){
             margin-top: 0; 
        }
    }

    // .item-cart__remove
    &__remove {
        @media (max-width:$md3+px){
             display: flex;
             justify-content:space-between; 
             max-width: 166px;
        }
    }

    // .item-cart__btn-remove
    &__btn-remove {
        background-color: transparent;
    }
}

.quantity {
    // .quantity__button
    &__button {

        // .quantity__button_plus
        &_plus {}

        // .quantity__button_minus
        &_minus {}
    }

    // .quantity__input
    &__input {}
}

.promocode {

    // .promocode__form
    &__form {
        @media (max-width:$md2+px){
            max-width: 480px;
        }
    }

    // .promocode__label
    &__label {
        margin-bottom: 6px;

        font-size: 13px;
        line-height: math.div(21px,13px) * 100%;
        color: var(--dark-gray);
    }

    // .promocode__input
    &__input {
        width: 100%;
        margin-bottom: 32px;

        border: 1px solid var(--light-white);

        @media (max-width:$md4+px){
             margin-bottom: 16px; 
        }

        input {
            width: 100%;
            padding: 13px 16px;
            font-size: 16px;
            line-height: math.div(26px,16px) * 100%;
            font-weight: 600;
            color: var(--add-gray);
        }
    }

    // .promocode__btn
    &__btn {
        width: 100%;
        padding: 10px 8px;

        line-height: math.div(26px,16px) * 100%;
        letter-spacing: math.div(.3px,16px) * 1em;
        font-weight: 800;

        color: var(--main-yellow);
        border: 2px solid var(--main-yellow);
        background-color: transparent;

        transition: color 0.3s ease 0s,
                    border-color 0.3s ease 0s;
        
        @media (any-hover: hover){
            &:hover{
                border-color: var(--hover-color);
                color: var(--hover-color);
            }
        }
        &:focus {
            border-color: var(--black-color);
        }
    }
}